/* eslint-disable no-unused-vars */
<template>
  <div id="app">
    <div class="topbar">
      <div class="topfix">
        <h1 class="topfl"></h1>
        <div class="topfr">APP</div>
      </div>
    </div>
    <ul
      id="nav"
      v-if="$route.meta.showNavBar"
    >
      <!-- class="animate__animated animate__fadeIn" -->
      <li>
        <router-link to="/"></router-link>
      </li>
      <li>
        <router-link to="/hot"></router-link>
      </li>
      <li>
        <router-link to="/search"></router-link>
      </li>
    </ul>

    <!-- @error Error in render: "TypeError: Cannot read properties of null (reading 'id')" -->
    <!-- @bug  currentSong null currentSong.id  -->
    <!-- @done currentSongcurrentSongId (id)currentSong.id, null -->
    <section class="routes">
      <transition
        name="custom-classes-transition"
        :enter-active-class="enterClass"
        :leave-active-class="leaveClass"
      >
        <!-- <keep-alive> -->
        <router-view
          @change-current-song="changeCurrentSong"
          @change-current-play-list="changeCurrentPlayList"
          @push-current-play-list="pushCurrentPlayList"
          :currentSongId='currentSong ? currentSong.id : null'
          :playing="playing"
          :currentSong="currentSong"
          style="position: absolute; top:0; left:0; width:100%; height:100%; overflow-y:auto;"
        ></router-view>
        <!-- </keep-alive> -->
      </transition>
    </section>
    <!-- audioplayingpausecontrols  autoplay  pause-->
    <!-- refdomvuedomjQueryDOM -->
    <audio
      ref="audio"
      :src="currentSongUrl"
      controls
      autoplay
      @playing="playing = true"
      @pause="playing = false"
      @timeupdate="timeupdate"
      @ended="ended"
      @loop="loopPlayback"
      @durationchange="durationchange"
    ></audio>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__slideInUp"
    >
      <Play
        v-if="currentSong"
        :currentSong="currentSong"
        :currentPlayList="currentPlayList"
        :playing="playing"
        :currentTime="currentTime"
        :currentVolume="currentVolume"
        :duration="duration"
        :playerMode="playerMode"
        :formerPlayList="formerPlayList"
        :collectionList="collectionList"
        :historyPlayList="historyPlayList"
        :presentPlayList="presentPlayList"
        @change-current-song="changeCurrentSong"
        @toggle-playing-state="togglePlayingState"
        @toggle-play-model="togglePlayModel"
        @previous-song="previousSong"
        @next-song="nextSong"
        @list-cycle="listCycle"
        @song-cycle="songCycle"
        @shuffle-play="shufflePlay"
        @current-time-change="$refs.audio.currentTime=$event"
        @current-volume-change="$refs.audio.volume=$event"
        @rank-play-list="rankPlayList"
        @del-new-play-list="delNewPlayList"
        @delete-the-item="deleteTheItem"
        @add-history-play-list="addHistoryPlayList"
        @del-all-collection-list="delAllCollectionList"
        @del-all-history-list="delAllHistoryList"
        @del-all-present-list="delAllPresentList"
        @add-collection="addCollection"
        @cancel-collection="cancelCollection"
      ></Play>
    </transition>
  </div>
</template>
<script>
import Play from './components/Play.vue'
import _ from "lodash";

export default {
  components: {
    Play,
  },
  data: function () {
    return {
      currentSong: null,
      currentPlayList: [],
      playing: false,
      currentTime: 0,
      currentVolume: 0.5,
      duration: 0,
      // conVolume: 1,
      index: '/home',
      enterClass: 'animate__animated animate__slideInRight',
      leaveClass: 'animate__animated animate__slideOutLeft',
      playerMode: 1,
      currentMode: 1,

      // 
      randomPlayList: [],
      formerPlayList: [],


      presentPlayList: [], //
      historyPlayList: [], //
      collectionList: [], //
    }
  },
  // 
  computed: {
    currentSongUrl: function () {
      if (this.currentSong) {
        return `https://music.163.com/song/media/outer/url?id=${this.currentSong.id}.mp3 `;
      } else {
        return null;
      }
    }
  },
  methods: {
    // 
    changeCurrentSong: function (song) {

      // this.historyPlayList = this.historyPlayList.concat(this.currentSong);
      // this.historyPlayList = [...new Set([...this.historyPlayList, this.currentSong])];
      var isLike = false;
      song.isLike = isLike;
      this.currentSong = song;
      this.judgeIsLike()
      // console.log('isLike', this.currentSong.isLike);
      var index = this.historyPlayList.findIndex((item) => {
        return item.id == this.currentSong.id;
      });
      // console.log('historyPlayList', index);
      // @done -1currentSong push(),key
      if (index == -1) {
        this.historyPlayList.push(this.currentSong);
        // lodash
        this.historyPlayList = _.uniqBy(this.historyPlayList, 'id');
      }

      localStorage.setItem('historyPlayList', JSON.stringify(this.historyPlayList)); //
      // console.log("", this.historyPlayList)
    },

    // (historyPlayList)
    addHistoryPlayList: function () {
      var index = this.presentPlayList.findIndex((item) => {
        return item.id == this.currentSong.id;
      });
      // console.log('currentPlayList', index);
      if (index == -1) {
        this.presentPlayList.push(this.currentSong);
        this.presentPlayList = _.uniqBy(this.presentPlayList, 'id');
      }
    },

    // 
    changeCurrentPlayList: function (list) {
      this.currentPlayList = list;
      this.formerPlayList = _.cloneDeep(this.randomPlayList);
      this.presentPlayList = _.cloneDeep(this.currentPlayList);

    },
    togglePlayingState: function () {
      if (this.playing) {
        // 
        this.$refs.audio.pause();
      } else {
        // 
        this.$refs.audio.play();
      }
    },
    timeupdate: function (event) {
      this.currentTime = event.target.currentTime;
    },
    durationchange: function (event) {
      this.duration = event.target.duration;
    },
    togglePlayModel: function () {
      // console.log("");
      this.playerMode++;
      if (this.playerMode > 3) {
        this.playerMode = 1;
      }

    },
    previousSong: function () {
      //  
      // item item.id  this.currentSong.id;
      var index = this.randomPlayList.findIndex((item) => {
        return item.id == this.currentSong.id;
      });
      index--;
      index = index > this.randomPlayList.length - 1 ? 0 : index;
      // @todo  this.randomPlayList.length - 1 index0
      index = index < 0 ? this.randomPlayList.length - 1 : index;
      // console.log(index);
      this.changeCurrentSong(this.randomPlayList[index]);


    },
    nextSong: function () {
      var index = this.randomPlayList.findIndex((item) => {
        return item.id == this.currentSong.id;
      });
      index++;
      index = index > this.randomPlayList.length - 1 ? 0 : index;
      index = index < 0 ? this.randomPlayList.length - 1 : index;
      // console.log(index);

      this.changeCurrentSong(this.randomPlayList[index]);
    },
    // 
    listCycle() {
      this.currentMode = 1;
      this.bus.$emit('tips', { show: true, title: '' });
      // console.log('',this.currentMode);

    },

    // 
    songCycle() {
      this.currentMode = 2;
      this.bus.$emit('tips', { show: true, title: '' });
      // console.log('',this.currentMode);

    },

    // 
    shufflePlay() {
      this.currentMode = 3;
      this.bus.$emit('tips', { show: true, title: '' });
      // console.log('',this.currentMode);
      this.randomSong();

    },
    //
    loopPlayback() {
      this.$refs.audio.play();
    },
    // 
    randomSong() {
      //  JSON 
      // this.randomPlayList = JSON.parse(JSON.stringify(this.currentPlayList));
      // lodash
      // console.log(_.chunk(["a", "b", "c", "d"], 2)); // ["a","b"],["c","d"]

      // var arr = this.currentPlayList;
      // var index = Math.floor(Math.random() * arr.length);
      // console.log(index);
      // this.changeCurrentSong(this.currentPlayList[index]);
      // @todo 
      // @done  lodash  _.cloneDeep() shuffle(),this.randomPlayList
      // currentPlayList
      var arr = _.cloneDeep(this.currentPlayList);
      this.randomPlayList = this.shuffle(arr);
      // console.log(this.randomPlayList);


    },
    shuffle: function (array) {
      var m = array.length, t, i;
      while (m) {
        i = Math.floor(Math.random() * m--);
        t = array[m];
        array[m] = array[i];
        array[i] = t;
      }
      return array;
    },

    // 
    ended: function () {
      switch (this.currentMode) {
        case 1:
          // randomPlayListcurrentPlayList;
          this.changeRandomPlayList();
          this.nextSong();
          break;
        case 2:
          this.loopPlayback();
          break;
        case 3:
          this.randomSong();
          this.nextSong();
          break;
      }
    },
    changeRandomPlayList: function () {
      this.randomPlayList = this.currentPlayList;
      this.formerPlayList = _.cloneDeep(this.randomPlayList);
      this.presentPlayList = _.cloneDeep(this.randomPlayList)
    },
    pushCurrentPlayList(value) {
      var index = this.currentPlayList.findIndex((item) => {
        return item.id == this.currentSong.id;
      });
      // console.log('', index);
      // currentPlayList
      if (index == -1) {
        this.currentPlayList.push(value);
        this.currentPlayList = Array.from(new Set([...this.currentPlayList]));
      }

    },

    //
    rankPlayList() {
      if (this.formerPlayList.length > 2) {

        this.formerPlayList = this.formerPlayList.reverse();
        // console.log('currentPlayList', this.formerPlayList);
      }
    },
    // (playlist)
    delNewPlayList() {
      this.formerPlayList = [];
      this.playing = false;
      this.currentSong = null;
      this.$refs.audio.pause();
    },

    // 
    // delListsSong(value, list) {
    //   // 
    //   console.log(value, list);
    //   if (value.item.id === list.id) {

    //     if (list.length >= 1) {
    //       var i = value.index;
    //       i = i >= list.length - 1 ? i = 0 : i++;

    //       this.changeCurrentSong(list[i]);
    //     } else if (list.length === 1) {
    //       this.changeCurrentSong(list[value.index]);
    //     } else {
    //       this.$refs.audio.pause();
    //     }
    //   }
    // },

    delAllListSong(list) {
      list.splice(0, list.length); //

    },

    // ()PagePlayList
    // delHistoryPlayList: function (val) {
    //   console.log('historyPlayList', val, val.index);
    //   console.log(val.item.id, this.currentSong.id);

    //   this.historyPlayList.splice(val.index, 1);

    //   this.delListsSong(val, this.historyPlayList);
    //   localStorage.setItem('historyPlayList', JSON.stringify(this.historyPlayList));

    // },
    // 
    deleteTheItem(val) {
      console.log('', val.type);
      switch (val.type) {
        case 1:
          // console.log('', val.type);
          // console.log('presentPlayList', val, val.index);
          // console.log(val.item.id, this.currentSong.id);

          this.presentPlayList.splice(val.index, 1);
          if (val.item.id === this.currentSong.id) {
            if (this.presentPlayList.length >= 1) {
              var i = val.index;
              i = i >= this.presentPlayList.length - 1 ? i = 0 : i++;
              this.changeCurrentSong(this.presentPlayList[i]);
            } else if (this.presentPlayList.length === 1) {
              this.changeCurrentSong(this.presentPlayList[val.index]);
            } else {
              this.$refs.audio.pause();
            }
          }
          if (this.presentPlayList.length === 0) {
            this.formerPlayList = [];
            this.playing = false;
            // this.currentSong = null;
            this.$refs.audio.pause();
          }
          this.listNull();
          break;
        case 2:
          // console.log('', val.type);
          // console.log('historyPlayList', val, val.index);
          // console.log(val.item.id, this.currentSong.id);

          this.historyPlayList.splice(val.index, 1);
          if (val.item.id === this.currentSong.id) {
            if (this.historyPlayList.length >= 1) {
              var index = val.index;
              index = index >= this.historyPlayList.length - 1 ? index = 0 : index++;
              this.changeCurrentSong(this.historyPlayList[index]);

              this.addHistoryPlayList();  // 
            } else if (this.historyPlayList.length === 1) {
              this.changeCurrentSong(this.historyPlayList[val.index]);
              this.addHistoryPlayList();
            } else {
              this.$refs.audio.pause();
            }
          }
          localStorage.setItem('historyPlayList', JSON.stringify(this.historyPlayList)); //
          if (this.historyPlayList.length === 0) {
            this.formerPlayList = [];
            this.playing = false;
            // this.currentSong = null;
            this.$refs.audio.pause();
          }
          this.listNull();
          break;
        case 3:
          // console.log('', val.type);
          this.collectionList.splice(val.index, 1);
          localStorage.setItem('collectionList', JSON.stringify(this.collectionList)); //
          break;
      }
    },

    // ()
    delAllCollectionList: function () {
      this.delAllListSong(this.collectionList);
      this.currentSong.isLike = false;
      localStorage.setItem('collectionList', JSON.stringify(this.collectionList));  //
      // this.listNull();
    },
    // ()
    delAllHistoryList: function () {
      this.delAllListSong(this.historyPlayList);
      localStorage.setItem('historyPlayList', JSON.stringify(this.historyPlayList)); //
      this.listNull();
    },
    // ()
    delAllPresentList: function () {
      this.delAllListSong(this.presentPlayList);
      // localStorage.setItem('presentPlayList', JSON.stringify(this.presentPlayList));
      this.listNull();
    },
    listNull() {
      if (this.presentPlayList.length == 0 && this.historyPlayList.length == 0) {
        this.formerPlayList = [];
        this.playing = false;
        this.currentSong = null;
        this.$refs.audio.pause();
      }
    },

    // 
    addCollection(like) {
      // console.log('isLike',like);
      this.currentSong.isLike = like;
      this.collectionList.push(this.currentSong);
      localStorage.setItem('collectionList', JSON.stringify(this.collectionList)); //

      // console.log(this.collectionList);
    },
    cancelCollection(islike) {
      this.currentSong.isLike = islike;
      var collectIndex = this.collectionList.findIndex((item) => {
        return item.id === this.currentSong.id;
      })
      this.collectionList.splice(collectIndex, 1);
      localStorage.setItem('collectionList', JSON.stringify(this.collectionList));  //
    },
    // 
    judgeIsLike() {
      var ii = this.collectionList.findIndex((item) => {
        return item.id === this.currentSong.id;
      })
      // console.log(ii);
      if (this.collectionList.length != 0) {

        if (ii == -1) {
          this.currentSong.isLike = false;
        } else {
          this.currentSong.isLike = true;
        }
      }
    },
  },
  watch: {
    $route(to, from) {
      // console.log(to.meta.index);//
      // console.log(from.meta.index);//
      // console.log(this.names);

      if (to.meta.index <= from.meta.index) {
        // this.names = "right"
        this.enterClass = 'animate__animated animate__slideInLeft';
        this.leaveClass = 'animate__animated animate__slideOutRight';
      } else {
        // this.names = "left",
        this.enterClass = 'animate__animated animate__slideInRight';
        this.leaveClass = 'animate__animated animate__slideOutLeft';
      }
    },
    //@bug  randomPlayList
    //@done currentPlayListrandomPlayList
    currentPlayList: function () {
      if (this.playerMode == 1) {
        this.changeRandomPlayList();
      }
    },
    playerMode: function () {
      if (this.playerMode == 1) {
        this.changeRandomPlayList();
      }
    },
    currentSong: function () {
      this.playing = false;
      // this.judgeIsLike();
    }
  },
  mounted() {
    this.$refs.audio.volume = 0.5;
    this.collectionList = JSON.parse(window.localStorage.getItem('collectionList')) || [];
    this.historyPlayList = JSON.parse(window.localStorage.getItem('historyPlayList')) || [];
    // console.log(this.historyPlayList);
  },

}
</script>


<style lang="less">
