<script>
// @ is an alias to /src
import HomeTitle from '@/components/HomeTitle.vue'
import CardListItem from '@/components/CardListItem.vue'
import SongListItem from '@/components/SongListItem.vue'



export default {

  name: "Home",
  components: {
    HomeTitle,
    CardListItem,
    SongListItem
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
    currentSong: Object,
  },
  data: function () {
    return {
      personalizeds: [],
      newsongs: [],
      isLoading: true,
    };
  },
  created() {
    // ajaxthis
    this.axios
      .get("/personalized?limit=6")
      .then((res) => {
        // console.log(this, res);
        this.personalizeds = res.data.result;
        // this.loaded();
      });
    this.axios
      .get("/personalized/newsong")
      .then((res) => {
        this.newsongs = res.data.result;
        this.loaded();
      });
  },
  methods: {
    loaded() {
      setTimeout(() => {
        this.isLoading = false;
      }, 200)
    }
  }
};
</script>
