<template>
  <div>
    <!--  -->
    <transition name="slide-fade">
    <div
      class="tips"
      v-show="tips.show"
    >
      <h3>{{tips.title}}</h3>
    </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {

      // tips
      tips: {
        show: false,
        title: '',
        timer: null,
      }
    }
  },
  created() {
    this.bus.$on('tips', (data) => {
      this.tips = data;
    })
  },
  watch: {
    // tips1.5s
    tips: function () {
// @bug ,,,
// settimeout
// @done  clearTimeout(this.timer)datatimer,
      clearTimeout(this.timer);
      if (this.tips.show) {
          this.timer =  setTimeout(() => {
          this.tips.show = false;
        }, 1500)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to{
  opacity: 0;
}
.tips {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: fixed;
  left: 0;
  top: 15rem;
  z-index: 299;
  width: 100%;
  height: 20%;
  color: #fff;
  h3 {
    width: 5.5rem;
    height: 2.2rem;
    padding: 0.2rem 0.3rem;
    font-size: 14px;
    line-height: 30px;
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 8px;
    text-align: center;
  }
}
</style>